<template>
	<div class="INlearnindex">
		<!-- 头部搜索 -->
		<div class="INsearch">
			<van-search
			  :label="$uerstore.store.state.addnametext"
			  shape="round"
			  background=""
			  v-model="value"
			  show-action
			  placeholder="请搜索您需要的课程"
			  @search="onSearch"
			>
			<van-icon style="float:right;" size="22px" slot="action" @click="userurl()" :name="iren" />
			</van-search>
			<div class="INsearchadd" @click="codeAddress()"></div>
		</div>
		<!-- 头图 -->
		<div class="INlearnindexHeader">
			<img class="INlearnindexBanner" :src="backbg" alt="">
		</div>
		<div style="width: 100%;background-color:#F7F7F7;height: 10px;"></div>
		<!-- 内容 -->
		<div class="INcontent">
			<!-- 选择导航 -->
			<div class="INcontentCon">
				<div class="INcontentConNav1">					
					<div style="width:20%;float: left;" v-for="(item,index) in nav1" :key="index">
						<span @click="clicknNav(index,item.id)" :class="cid==item.id?'boxnext':''">{{item.name}}</span>
					</div>
				</div>
				<!-- 内容块 -->	
				<div class="INcontentConList">
					<!-- 列表组件 -->
					<DetailsList v-bind:conList="conList" v-on:detalisid="detalisid"></DetailsList>
					<!-- 更多 -->
					<div class="INcontentConListTL" @click="inmoer()">
						<img src="../assets/bmin.png" alt="">
					</div>
				</div>
			</div>
			<!-- 浮动框 -->
			<flastbox></flastbox>
			<!-- 公众号弹出窗 -->
			<div class="GZHPopupTC" v-show="GZHPopup" @click="gbcode()"></div>
			<div class="GZHPopupCON" v-show="GZHPopup">
				<img class="GZHPopupCONcode" src="https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/YXTTWM.png" alt="">
				<div class="GZHPopupCONtit">请扫码关注公众号</div>
				<div class="GZHPopupCONtext">如未关注公众号将无法收到推送消息</div>
			</div>
		</div>
		<!-- 底部 -->
		<bottonBox></bottonBox>
	</div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';
	import DetailsList from "./details/details.vue"; //列表组件
	import flastbox from "./details/flastbox.vue"; //分享
	import bottonBox from "./details/bottonBox.vue"; //底部
	
	export default {
		components: {
			DetailsList,
			flastbox,
			bottonBox
		},		
		data() {
			return {
				iren:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/ren.png',//小人
				backbg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/indexbg.png',//背景
				value:'',//搜索内容
				nav1:[],//第一模块导航
				addstate:'[options: {timeout:1s, failTipFlag: boolean}]',//定位设置
				conList:[],//机构信息
				result: [],
				cid :'',// 类目
				aid:'',//年龄
				GZHPopup:false,
			}
		},
		watch:{
			'$uerstore.store.state.addnametext':function(newFlag, oldFlag){
				//信息列表
				this.Aajaxnav();
			},
			'$uerstore.store.state.GZHcodestate':function(newFlag, oldFlag){
				//信息列表
				if (newFlag==2) {
					this.GZHPopup=true;
				};
			} 
		},
		methods: {
			// 个人中心
			userurl(){
				this.$router.push({path:'/user'});
			},
			// 搜索
			onSearch(val) {
			      Toast(val);
			},
			//关闭公众好二维码弹窗
			gbcode(){
				this.GZHPopup= false
			},
			//更多
			inmoer(){
				//更多跳转
				this.$uerstore.INnav1 = this.nav1;
				
			},
			//导航切换
			clicknNav(index,id) {
				this.cid = id;
				this.$uerstore.store.state.INnav1state.id = id;//存入vuexid
				// 重新获取机构列表
				this.JGList();
			},
			// 弹出层数据传递课程
			detalisid(e) {
				console.log(e);
				this.$router.push({path:'/brief',query:{id:e}});
			},
			// A导航请求
			Aajaxnav(){
				this.axios.post('CategoryList').then((response)=>{
				    // ...
					this.nav1 = response.data.data;
					//判断识别
					this.cid = response.data.data[0].id;
					this.$uerstore.store.state.INnav1state.id = response.data.data[0].id;
					if (this.$uerstore.store.state.INnav1state.id!='') {
						this.cid = this.$uerstore.store.state.INnav1state.id;//存入vuexid
					};
					// 获取机构列表
					this.JGList();
				})
			},
			//机构列表
			JGList(){
				let thisdata = {
					latitude:localStorage.getItem('INlatitude'),
					longitude:localStorage.getItem('INlongitude'),
					cid :this.cid,
					aid:this.aid,
				};
				this.axios.post('getMember',this.qs.stringify(thisdata)).then((response)=>{
				    // ...
					this.conList = [];
					var newbox = [];
					for (var i = 0; i < response.data.data.length; i++) {
						newbox.push(response.data.data[i]);
						if (newbox.length >= 4) {
							this.conList.push(newbox);
							newbox=[];
						};
					};
					// console.log('新的数值对象',this.conList);
					console.log('机构列表',response.data.data);
					// 验证报名成功是否是成功返回
					this.baomfH();
				})
			},
			//详细地址转换成经纬度
			codeAddress(){
			   let url = window.location.href;
			   window.location.href = 'http://apis.map.qq.com/tools/locpicker?search=1&type=0&backurl='+url+'&key=AZ6BZ-G5WWO-UGOWQ-STA4N-NQG72-2TB4X&referer=因学教育';
			},
			//首页禁止返回
			goBack () {
			   // console.log("点击了浏览器的返回按钮");
			   history.pushState(null, null, document.URL);
			},
			//是否是报名成功之后返回
			baomfH(){
				if (this.$uerstore.store.state.SuccessBAOMid !=0) {
					this.$uerstore.store.commit("SuccessBAOM",0);//状态写入
					window.location.href = 'https://yxhd.insooner.com/dista?urluid='+localStorage.getItem('INcodeuid')+'&GZHcodestate='+this.$uerstore.store.state.GZHcodestate;
				};
			}
		},
		mounted() {
			//无法获取定位信息列表
			if (localStorage.getItem('INlatitude')==undefined || localStorage.getItem('INlatitude')==null ||localStorage.getItem('INlatitude')=='') {
				localStorage.setItem('INlatitude','30.230595');//纬度
				localStorage.setItem('INlongitude','120.213776');//经度
			};
			this.Aajaxnav();
			if (this.$uerstore.store.state.INnav1state.id!='') {
				this.cid = this.$uerstore.store.state.INnav1state.id;//存入vuexid
			};
			//清楚更多搜索
			this.$uerstore.store.state.inputname = '';
			// 禁止返回
			if (window.history && window.history.pushState) {
				// 向历史记录中插入了当前页
				history.pushState(null, null, document.URL);
				window.addEventListener('popstate', this.goBack, false);
			}
		},
		destroyed () {
			//页面销毁返回销毁
		    window.removeEventListener('popstate', this.goBack, false);
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.INsearch{
		width: 100%;
		position: fixed;
		z-index: 99;
	}
	.INsearchadd{
		position: absolute;
		top: 0;
		left: 0;
		width:70px;
		height: 54px;
	}
	.GZHPopupTC{
		position: fixed;
		width: 100%;
		height: 100%;
		background-color: rgba(0,0,0,0.5);
		z-index: 99;
		top: 0;
		left: 0;
	}
	.GZHPopupCON{
		position: fixed;
		width:240px;
		height: 300px;
		background-color: #fff;
		border-radius: 12px;
		top: 50%;
		left: 50%;
		margin-top: -150px;
		margin-left: -120px;
		z-index: 100;
	}
	.GZHPopupCONcode{
		width: 180px;
		height: 180px;
		display: block;
		margin: 30px 30px 12px 30px;
	}
	.GZHPopupCONtit{
		font-size: 16px;
		color: #000;
	}
	.GZHPopupCONtext{
		font-size: 12px;
		color: #999999;
		line-height: 40px;
	}
	/* 内容 */
	h1,
	h2 {
		font-weight: normal;
	}

	ul {
		list-style-type: none;
		padding: 0;
	}

	li {
		display: inline-block;
		margin: 0 10px;
	}

	a {
		color: #42b983;
	}

	.INlearnindex {
		width: 100%;
		background-size:30px 30px;
	}

	.INlearnindexHeader {
		width: 100%;
		display: block;
		position: relative;
	}

	.INlearnindexLoge {
		position: absolute;
		height: 24px;
		top: 13px;
		left: 13px;
	}

	.INlearnindexBanner {
		width: 100%;
		display: block;
	}

	.INlearnindexAdds {
		position: absolute;
		left: 0;
		bottom:8px;
		width: 100%;
		height: 20px;
	}

	.INlearnindexAdds img {
		width: 20px;
		float: left;
		display: block;
		margin: 0 12px 0 16px;
	}

	.INlearnindexAdds p {
		float: left;
		font-size: 14px;
		color: #fff;
		margin: 0;
		width: 80%;
		height: 20px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		text-align: left;
	}

	.INcontent {
		/* width: 100%; */
		margin-top: 0;
		padding: 0 14px;
	}

	.INcontentCon {
		width: 100%;
		background-color: #FFFFFF;
		border-radius: 14px;
		position: relative;
		z-index: 2;
		overflow: hidden;
	}

	.INcontentConNav1 {
		width: 100%;
		padding: 14px 5px 7px 5px;
		overflow: hidden;
	}

	.INcontentConNav2 {
		width: 100%;
		padding: 7px 5px 14px 5px;
		overflow: hidden;
		box-shadow: 0px 1px 0px 0px rgba(235, 235, 235, 1);
	}

	.INcontentConNav1 span,
	.INcontentConNav2 span {
		float: left;
		padding: 4px 12px;
		background: rgba(247, 247, 247, 1);
		border-radius: 50px;
		font-size: 14px;
		color: #999999;
		margin-bottom: 10px;
	}

	.INcontentConList {
		width: calc(100% - 14px);
		padding: 14px 7px;
		overflow: hidden;
	}

	/* 报名提交 */
	.INcontentConListTL {
		width: 100%;
		margin-top: 16px;
	}

	.INcontentConListTL img {
		width: 100%;
		display: block;
	}

	/* 更多 */
	.INcontentConMORE {
		font-size: 16px;
		color: #333333;
	}

	/* 选中按钮 */
	.boxnext {
		background-color: #29bf5f !important;
		color: #FFFFFF !important;
	}
	
	/* 遮罩 */
	.sharebgzhao{
	  background-color: rgba(0,0,0,0.5);
	  width: 100%;
	  height: 100%;
	  position: fixed;
	  top: 0;
	  left: 0;
	  z-index: 99;
	}
	.INcontentmoer{
		font-size: 14px;
		line-height: 30px;
		text-decoration:underline;
	}
</style>



