<template>
	<div style="width: 100%;height: 100%;">
		<div class="insuccess">
			<div class="insuccessOne">
				<div class="insuccessOnelift">
					<img src="../../assets/ok.png" alt="">
				</div>
				<div class="insuccessOnerigth">
					<div class="insuccessOnerigthtext">恭喜您课程报名成功</div>
					<div class="insuccessOnerigthorderid">订单号：<span>{{$route.query.id}}</span></div>
					<div class="insuccessOnerigthmud">预约机构：<span>{{$route.query.mud}}</span></div>
				</div>
			</div>
			<div class="insuccesswto">
			  <div class="insuccesswtolift">
				<p>想要或者更多免费课程</p>
				<p>请关注因学教育小程序</p>
			  </div>
			  <div class="insuccesswtoright">
				<img src="../../assets/ma.png" alt="">
			  </div>
			</div>
			<!-- 弹窗 -->
			<!-- <van-popup v-model="show" style="width:300px;background-color: inherit;height:332px;">
				<img class="INpopupimg" src="../../assets/tfff.png" alt="" @click="Sharegape()">
				<div style="line-height: 100px;font-size:36px;color:rgba(255,255,255,1);"><van-icon name="close" @click="showPopup" /></div>
			</van-popup> -->
			<div v-show="show" class="INzzhao" @click="showPopup()" ></div>
			<div v-show="show"  class="INzzhaocon">
				<img class="INpopupimg" src="../../assets/tfff.png" alt="" @click="Sharegape()">
				<div style="line-height: 100px;font-size:36px;color:rgba(255,255,255,1);"><van-icon name="close" @click="showPopup()" /></div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
	      show: true
	    };
	  },
		methods:{
		   showPopup() {
		      this.show = false;
		    },
			//分享页面跳转
			Sharegape(){
				window.location.href = 'https://yxhd.insooner.com/dista?urluid='+localStorage.getItem('INcodeuid');
			}
		}
	};
</script>

<style scoped>
	/* 遮罩 */
	.INzzhao{
		position: fixed;
		background-color: rgba(0,0,0,0.5);
		width: 100%;
		height: 100%;
		top:0;
		left: 0;
	}
	.INzzhaocon{
		position: fixed;
		width:300px;
		top: 30%;
		left: 50%;
		margin-left: -150px;
	}
	/* 内容 */
	.insuccess{
		padding: 14px;
	}
	.insuccessOne{
		background-color: #FFFFFF;
		border-radius: 12px;
		padding: 20px;
		margin-bottom: 14px;
		overflow: hidden;
	}
	.insuccessOnelift{
		float: left;
		width: 90px;
		height: 90px;
	}
	.insuccessOnelift img{
		width: 100%;
		display: block;
	}
	.insuccessOnerigth{
		float: left;
		text-align: left;
		line-height: 30px;
		margin-left: 12px;
	}
	.insuccessOnerigthtext{
		font-size: 16px;
		color: #333;
	}
	.insuccessOnerigthorderid{
		font-size: 14px;
		color: #999999;
	}
	.insuccessOnerigthorderid span{
		color: #333333;
	}
	.insuccessOnerigthmud{
		font-size: 14px;
		color: #999999;
	}
	.insuccessOnerigthmud span{
		color: #E14901;
	}
	/* 第二部分 */
	.insuccesswto{
		background-color: #FFFFFF;
		border-radius: 12px;
		padding: 20px;
		margin-bottom: 14px;
		overflow: hidden;
	}
	.insuccesswtolift{
		width: 150px;
		color: #736E6B;
		font-size: 14px;
		float: left;
		margin-top: 40px;
	}
	.insuccesswtolift p{
		margin: 6px 0;
	}
	.insuccesswtoright{
		width: 140px;
		height: 140px;
		float: right;
	}
	.insuccesswtoright img{
		width: 100%;
		display: block;
	}
	.INpopupimg{
		width: 100%;
		display: block;
	}
</style>



