import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'//首页
import brief from '@/components/brief/brief'//机构详情
import briefcon from '@/components/brief/briefcon'//机构课程详情
import car from '@/components/car/car'//购物车
import Order from '@/components/car/Order'//活动报名
import inSuccess from '@/components/car/Success'//活动报名
import inShare from '@/components/Share/Share'//分享
import inmoreCon from '@/components/moreCon/morecon'//更多
import Rules from '@/components/Share/Rules'//更多
import JGorder from '@/components/JGorder/JGorder'//已报名机构
import user from '@/components/user/user'//已报名机构
Vue.use(Router)

export default new Router({
	mode: 'history',
	base: '/INContrarian/',
    routes: [
    {
      path: '/',
      name: 'index',
      component: index,
	    meta:{index:0}
    },
	{
	  path: '/inShare',
	  name: 'inShare',
	  component: inShare,
	  meta:{index:6},
	},
	{
	  path: '/Rules',
	  name: 'Rules',
	  component: Rules,
	  meta:{index:7},
	},
	{
	  path: '/inmoreCon',
	  name: 'inmoreCon',
	  component: inmoreCon,
		meta:{index:1},
	},
	{
	  path: '/JGorder',
	  name: 'JGorder',
	  component: JGorder,
		meta:{index:1},
	},
	{
	  path: '/brief',
	  name: 'brief',
	  component: brief,
		meta:{index:2},
	},
	{
		path: '/briefcon',
		name: 'briefcon',
		component: briefcon,
		meta:{index:3},
	},	
	{
	  path: '/car',
	  name: 'car',
	  component: car,
		meta:{index:4},
	},
	{
	  path: '/Order',
	  name: 'Order',
	  component: Order,
		meta:{index:5},
	},
	{
	  path: '/inSuccess',
	  name: 'inSuccess',
	  component: inSuccess,
		meta:{index:6},
	},
	{
	  path: '/user',
	  name: 'user',
	  component: user,
		meta:{index:7},
	},
  ]
})



