<template>
	<div class="incar">
		<div class="incarBox">
			<van-checkbox-group v-model="result" v-for="(item, index) in list" :key="index">
				<div class="incarBoxklist">
					<div class="incarBoxklistlift">
						<van-checkbox checked-color="#FF6714" class="incarBoxklistliftcheckbox" :key="item.id" :name="item"></van-checkbox>
					</div>
					<div class="incarBoxklistZ">
						<img :src="item.img" alt="">
						<div class="incarBoxklistZCOn">
							<p style="font-size:14px;line-height:30px;height: 30px;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{item.cname}}</p>
							<p style="margin-bottom: 16px;"><span class="incarBoxklistZCOnvac">{{item.csname}}</span></p>
							<p class="incarBoxklistZCOnmud">剩余名额：<span>{{item.re_num}}</span></p>
						</div>
					</div>
					<div class="incarBoxklistright">
						<img src="../../assets/delet.png" alt="">
						<span @click="delbox(item.id)">删除</span>
					</div>
				</div>
			</van-checkbox-group>
			<div class="wuobjkt" v-show="list.length==0"><span>暂无机构</span></div>
		</div>
		<!-- 提交 -->
		<div class="incarbuttom">
			<div class="incarbuttomcheckbox"><span>已选{{this.result.length}}项</span></div>
			<div class="incarbuttombut" @click="boxOK()">确认报名</div>
			<div class="incarbuttomdel" @click="delboxs()">删除</div>
		</div>
	</div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';
	export default {
		data() {
			return {
				maxmud: 0,
				list: [],//列表内容
				result: [],
				TJlist:[],//报名数组
			};
		},
		methods: {
			//单个删除
			delbox(e) {
				// console.log(e);
				Dialog.confirm({
				  title: '是否真的需要删除！',
				  // message: '弹窗内容'
				}).then(() => {
				  // on confirm
				  let ss = [];
				  ss.push(e);
				  this.carlistdel(ss);
				}).catch(() => {
				  // on cancel
				});
			},
			//全部选择删除
			delboxs() {
				console.log(this.result);
				if (this.result=='') {
					return Toast("您当前还没有选择机构！")
				};
				Dialog.confirm({
				  title: '是否真的需要删除！',
				  // message: '弹窗内容'
				}).then(() => {
				  // on confirm
				  let objlist = [];
				  this.result.forEach((val)=>{
					 objlist.push(val.id);
				   });
				  this.carlistdel(objlist);
				}).catch(() => {
				  // on cancel
				});
			},
			//确认报名
			boxOK() {
				// console.log(this.result)；
				if (this.maxmud == 0) {
					return Toast("您当前可报名的机构名额已满！");
				};
				if (this.result=='') {
					return Toast("您当前还没有选择机构！")
				} else if(this.result.length>this.maxmud){
					return Toast("您当前选择机构超过"+this.maxmud+"家，请选择您最喜欢的机构吧！");
				}else{
					if (this.result.length<this.maxmud) {
						Dialog.confirm({
						  message: '您当前选择的机构数量为'+this.result.length+'家，您还可选择'+(this.maxmud*1-this.result.length*1)+'家，是否需要继续？',
						  // message: '弹窗内容'
						}).then(() => {
						  // on confirm
						  this.orderlist();
						}).catch(() => {
						  // on cancel
						});	
					} else{
						this.orderlist();
					}
				}
			},
			//加入订单
			orderlist(){
				let ides = [];
				this.result.forEach(item =>{
				    ides.push(item.mid);
				});
				this.$router.push({
					path: '/Order',
					query: {
						id: ides,
						catType:2,
						uid:localStorage.getItem('INcodeuid')
					}
				});
			},
			// 获取购物车数据
			carajaxlist(){
				let dataS={
					id:localStorage.getItem('INcodeuid')
				};
				this.axios.post('getCat',this.qs.stringify(dataS)).then((response)=>{
				    // ...
					console.log('购物车',response.data);
					this.list = response.data.data.res;
					this.result = response.data.data.res;
					this.maxmud = response.data.data.type;
				})
			},
			// 删除机构
			carlistdel(e){
				console.log(e);
				let dataSdel={
					id:e,
				};
				this.axios.post('delCat',this.qs.stringify(dataSdel)).then((response)=>{
				    // ...
					Toast(response.data.msg);
					this.result=[];
					this.carajaxlist();
				})
			},
			//订单生成
			//分享用户授权
			shareures(){
				const INUserID = this.$uerstore.store.state.BDuidTHIS;//传入用户id
				const INUserMID = this.$uerstore.store.state.GJmidTHIS;//传入机构id
				if (INUserID==undefined&&INUserMID==undefined) return;
				if (INUserID==undefined || INUserID==null || INUserID=='') {
					let thisdata = {
						id:localStorage.getItem('INcodeuid'),
						inviteid:'',
						latitude:localStorage.getItem('INlatitude'),
						longitude:localStorage.getItem('INlongitude'),
						mid:INUserMID
					};
					this.axios.post('setDistribution',this.qs.stringify(thisdata)).then((response)=>{
					    // ...
						console.log('分享信息获取',response);
					});
				}else if(INUserMID==undefined || INUserMID==null || INUserMID==''){
					let thisdata = {
						id:localStorage.getItem('INcodeuid'),
						inviteid:INUserID,
						latitude:localStorage.getItem('INlatitude'),
						longitude:localStorage.getItem('INlongitude'),
						mid:''
					};
					this.axios.post('setDistribution',this.qs.stringify(thisdata)).then((response)=>{
					    // ...
						console.log('分享信息获取',response);
					});	
				}
				
			}
		},
		watch: {
			//全选需要
			result(e) {
				// if (e == this.list || e.length == this.maxmud) {
				// 	e.length == this.maxmud ? Toast('您当前的选课数量已达到最大的' + this.maxmud + '项') : '';
				// 	this.resultbuttom = true;
				// } else {
				// 	this.resultbuttom = false;
				// };
			}
		},
		mounted() {
			this.carajaxlist();
			this.shareures();
		}
	};
</script>

<style scoped>
	.incar {
		width: 100%;
		height: 100%;
		background: rgba(250, 250, 249, 1);
	}

	.incarBox {
		padding: 10px 14px 64px 14px;
	}

	.incarBoxklist {
		width: 100%;
		border-radius: 10px;
		background-color: #fff;
		height: 108px;
		margin-bottom: 10px;
		position: relative;
	}

	.incarBoxklistlift {
		height: 100%;
		width: 58px;
		float: left;
	}

	.incarBoxklistliftcheckbox {
		float: left;
		margin: 44px 18px;
	}

	/* 内容 */
	.incarBoxklistZ {
		width: 60%;
		margin-top: 12px;
		height: 84px;
		float: left;
	}

	.incarBoxklistZ img {
		width: 84px;
		height: 84px;
		border-radius: 10px;
		float: left;
	}

	.incarBoxklistZCOn {
		float: left;
		width: calc(100% - 94px);
		margin-left: 10px;
	}

	.incarBoxklistZCOn p {
		width: 100%;
		margin: 0;
		text-align: left;
	}

	.incarBoxklistZCOnvac {
		color: #736E6B;
		font-size: 12px;
		background-color: #F7F7F7;
		padding: 0 10px;
		border-radius: 10px;
		line-height: 20px;
	}

	.incarBoxklistZCOnmud {
		font-size: 12px;
		color: #999999;
	}

	.incarBoxklistZCOnmud span {
		color: #FFA02D;
	}

	.incarBoxklistright {
		position: absolute;
		right: 10px;
		top: 10px;
	}

	.incarBoxklistright img {
		width: 16px;
		height: 16px;
		display: block;
		float: left;
	}

	.incarBoxklistright span {
		font-size: 12px;
		float: left;
		margin-left: 6px;
		color: #999999;
	}

	.incarbuttom {
		width: 100%;
		height: 54px;
		background-color: #fff;
		position: fixed;
		bottom: 0;
		left: 0;
	}

	/* 底部栏 */
	.incarbuttomcheckbox {
		float: left;
		margin: 18px 14px;
	}

	.incarbuttomcheckbox span {
		color: #999999;
		margin-left: 6px;
	}

	.incarbuttomdel {
		float: right;
		color: #E14901;
		line-height: 54px;
		font-size: 12px;
	}

	.incarbuttombut {
		float: right;
		width: 134px;
		height: 44px;
		font-size: 16px;
		line-height: 44px;
		margin: 5px 14px;
		background: linear-gradient(90deg, rgba(255, 160, 45, 1) 0%, rgba(255, 103, 20, 1) 100%);
		color: #fff;
		border-radius: 44px;
	}
	.wuobjkt{
		margin-top:80%;
	}
	.wuobjkt span{
	   border: 1px #999 solid;
	   padding: 6px 20px;
	   color: #999999;
	   border-radius:50px;
	}
</style>



