<template>
	<div class="brief">
		<!-- 顶部内容 -->
		<div style="padding: 12px;overflow: hidden;background-color: #fff;">
			<div  class="detnavimg">
				<img v-lazy="conlist.img" >
			</div>
			<div class="detnavcon">
				<div class="detnavconlogo">
					<img v-lazy="conlist.img">
				</div>
				<div class="detnavcontext">
					<div class="detnavcontexttit">{{conlist.cname}}</div>
					<div class="detnavcontextdex">{{conlist.remarks}}</div>
					<div class="detnavcontextM">{{$uerstore.addjl(conlist.distance)}}m</div>
				</div>
			</div>
			<div class="brief_bq">
				<p>思维训练</p>
				<p>英语</p>
				<p>4-12岁</p>
			</div>
			<div style="width: 100%;float: left;font-size: 14px;color: #333;text-align: left;color: #999999;">浏览量：999</div>
		</div>
		<!-- 内容 -->
		<div class="briefTit" style="position: relative;">
			<div class="briefaddes" @click="addDQ()"><van-icon name="location-o" style="font-size: 16px;padding-right:6px;" />地址：<span>{{conlist.address}}</span></div>
			<div class="briefTittel" @click="callPhone(conlist.phone)">
				<van-icon :name="tellog" size="20px" />
				<div style="font-size: 12px;">咨询</div>
			</div>
		</div>
		<!-- 课程列表 -->
		<div class="briefJJ">
			<div class="detnav">我要选课</div>
			<div class="briefJJimg" v-for="(item,index) in 4" :key="index" @click="detJG($route.query.id)">
				<van-card thumb="https://img.yzcdn.cn/vant/ipad.jpeg">
				  <div slot="tags">
				    <div class="briefJJtit">我要选课</div>
					<div class="briefJJtitru" style="color: #999999;">我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课</div>
				  </div>
				</van-card>
			</div>
		</div>
		<!-- 浮动框分享 -->
		<!-- <div class="flastcon">			  
			<div style="position: relative;">
				<div class="yuanIN" v-show="carlenght!=0">{{carlenght}}</div>
			    <img @click="carcont()" class="imgbox" src="../../assets/icon/icar.png" alt=""> 
			</div>	
		</div> -->
	</div>
</template>

<script>
	import { Toast } from 'vant';
	export default {
		data() {
			return {
				conlist:[],
				mud:'',//剩余名额
				mudes:'',//剩余数量
				JGLon:'',//经度
				JGlat:'',//纬度
				carlenght:'',
				orderStatus:'1',//购买状态
				tellog:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/phone.png'//电话图标
			}
		},
	  methods: {
		//进入机构课程详情
		detJG(e){
		  this.$router.push({path:'/briefcon',query:{id:e}});
		}, 
		//拨打电话
		callPhone(e){
			window.location.href = 'tel://'+e
		},
		addDQ(){
			// 地图调用
			window.location.href="https://apis.map.qq.com/uri/v1/routeplan?type=walk&from=我的位置&fromcoord="+localStorage.getItem('INlatitude')+","+localStorage.getItem('INlongitude')+"&to=机构位置&tocoord="+this.JGlat+","+this.JGLon+"&policy=3&referer=AZ6BZ-G5WWO-UGOWQ-STA4N-NQG72-2TB4X"
		},
		Conajax(){
			let dataM={
				id:this.$route.query.id,
				latitude:localStorage.getItem('INlatitude'),
				longitude:localStorage.getItem('INlongitude'),
				uid:localStorage.getItem('INcodeuid')
			};
			this.axios.post('getMemberInfo',this.qs.stringify(dataM)).then((response)=>{
			    // ...
				this.conlist = response.data.data;
				this.mud ="剩余名额："+ response.data.data.re_num + "人";
				this.mudes = response.data.data.re_num;
				this.JGLon = response.data.data.longitude;
				this.JGlat = response.data.data.latitude;
				this.orderStatus =response.data.data.order_status;
				console.log('机构详情', response.data.data);
			})
		},
		//购物车数量
		carajaxlist(){
			let dataS={
				id:localStorage.getItem('INcodeuid')
			};
			this.axios.post('getCat',this.qs.stringify(dataS)).then((response)=>{
			    // ...
				console.log('购物车长度数据',response.data.data);
				this.carlenght = response.data.data.res.length;
			})
		},
		//进入购物车
		carcont(){
			this.$router.push({path:'/car'});
		},
		//分享用户授权
		shareures(){
			const INUserID = this.$uerstore.store.state.BDuidTHIS;//传入用户id
			const INUserMID = this.$uerstore.store.state.GJmidTHIS;//传入机构id
			if (INUserID==undefined&&INUserMID==undefined) return;
			if (INUserID==undefined || INUserID==null || INUserID=='') {
				let thisdata = {
					id:localStorage.getItem('INcodeuid'),
					inviteid:'',
					latitude:localStorage.getItem('INlatitude'),
					longitude:localStorage.getItem('INlongitude'),
					mid:INUserMID
				};
				this.axios.post('setDistribution',this.qs.stringify(thisdata)).then((response)=>{
				    // ...
					console.log('分享信息获取',response);
				});
			}else if(INUserMID==undefined || INUserMID==null || INUserMID==''){
				let thisdata = {
					id:localStorage.getItem('INcodeuid'),
					inviteid:INUserID,
					latitude:localStorage.getItem('INlatitude'),
					longitude:localStorage.getItem('INlongitude'),
					mid:''
				};
				this.axios.post('setDistribution',this.qs.stringify(thisdata)).then((response)=>{
				    // ...
					console.log('分享信息获取',response);
				});	
			}
			
		}
	  },
	  mounted() {
	  	this.Conajax();
		this.carajaxlist();
		this.shareures();
	  },
	}
</script>

<style scoped>
	.briefTittel{
		width: 60px;
		position: absolute;
		right: 0;
		top:2px;
	}
	.briefJJtitru{
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
		font-size: 14px;
	}
	.briefJJtit{
		line-height: 27px;
		height: 27px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 14px;
	}
	.brief_bq{
		width: 100%;
	}
	.brief_bq p{
		float:left;
		border: 1px #41b180 solid;
		border-radius: 4px;
		font-size: 12px;
		color: #41b180;
		margin-right: 5px;
		padding: 2px 5px;
	}
	.detnavcon{
		width: 100%;
	}
	.detnavcontext{
		width: calc(100% - 74px);
		float: left;
		margin-left: 20px;
		text-align: left;
		position: relative;
	}
	.detnavcontextM{
		position: absolute;
		bottom: 0;
		right: 0;
		line-height: 27px;
		font-size: 12px;
	}
	.detnavcontexttit{
		line-height: 27px;
		height: 27px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.detnavcontextdex{
		width: 70%;
		line-height: 27px;
		font-size: 12px;
		color: #999999;
		height: 27px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.detnavconlogo {
		width: 54px;
		height: 54px;
		overflow: hidden;
		border-radius: 50%;
		float: left;
	}
	.detnavconlogo img{
		width: 100%;
		display: block;
	}
	.detnav{
		font-size:16px;
		font-weight: bold;
		text-align: left;
		position: relative;
		padding-left:12px;
		color: #333333;
		margin-bottom:20px;
	}
	.detnav::after{
		position: absolute;
		top: 0;
		left: 0;
		width: 4px;
		height: 21px;
		content: '';
		background:linear-gradient(313deg,#29bf5f 0%,#b2da9a 100%);
		border-radius:10px;
	}
	.detnavimg{
		width: 100%;
		max-height:180px;
		overflow: hidden;
		border-radius: 12px;
		margin-bottom: 12px;
	}
	.detnavimg img{
		width: 100%;
		display: block;
	}
	
	
	
	.yuanIN{
		position: absolute;
		top: 0;
		right: 4px; 
		width: 20px;
		height: 20px;
		border-radius: 50%;
		background-color: #ee0a24;
		line-height: 20px;
		text-align: center;
		font-size: 12px;
		color: #fff;
	}
	.brief{
		width: 100%;
		background-color: #FAFAF9;
		height: 100%;
	}

	.briefTit{
		padding: 0 14px;
		background-color: #fff;
		margin: 10px 0;
	}
	.briefaddes{
		width: 100%;
		height:45px;
		font-size: 13px;
		text-align: left;
		color: #333;
		line-height: 45px;
	}
	.briefaddes span{
		color: #666;
	}

	/* 简介 */
	.briefJJ{
		padding: 20px 14px;
		background-color: #fff;
		margin-bottom: 10px;
	}
	.briefJJTit{
		height: 44px;
		text-align: left;
		line-height: 44px;
		color:rgba(51,51,51,1);
		font-weight: bold;
	}
	.briefJJTit img{
		width: 22px;
		height: 22px;
		float: left;
		margin: 11px 10px 0 0;
	}
	.briefJJTEXT{
		font-size: 14px;
		color: #736E6B;
		text-align: left;
		margin-bottom: 60px;
		padding-bottom:20px;
	}
	/* 图片 */
	.briefJJimg{
		text-align: left;
		margin-bottom: 20px;
	}
	.briefbuttom{
		width: 100%;
		height: 54px;
		background-color: #fff;
		position: fixed;
		bottom: 0;
		left: 0;
	}
	/* 分享 */
	.flastcon{
	  position:fixed;
	  bottom: 200px;
	  right: 24px;
	  width: 54px;
	  z-index:2;
	}
	.imgbox{
	  width: 54px;
	  height: 54px;
	}
	.sharebg{
		  background-color: rgba(0,0,0,0.5);
		  width: 100%;
		  height: 100%;
		  position: fixed;
		  top: 0;
		  left: 0;
	}
	.imgboxdd{
		  -webkit-animation: seconddiv 2s infinite linear;
		  background: transparent;	
	}
	
	@keyframes seconddiv{
		0% {transform: scale(1,1);}
		10% {transform: scale(1,1);}
		25% {transform: scale(1.2,1.2);}
		50% {transform: scale(1,1);}
		70% {transform: scale(1.2,1.2);}
		100% {transform: scale(1,1);}
	}
</style>
<style>
	.briefJJimg img{
		width: 100%;
		height: auto;
		display: block;
	}
</style>


