<template>
	<div style="overflow: hidden;">
		<div class="detnav">我要选课</div>
		<div v-for="(items,index) in conList" :key="index">
			<div v-for="(item,index) in items" :key="index"  style="margin-bottom: 20px;overflow:hidden;" @click="clickindex(item.uid,item.cname)">
				<div  class="detnavimg">
					<img v-lazy="item.img" >
				</div>
				<div class="detnavcon">
					<div class="detnavconlogo">
						<img v-lazy="item.img">
					</div>
					<div class="detnavcontext">
						<div class="detnavcontexttit">{{ item.cname }}</div>
						<div class="detnavcontextdex">{{ item.remarks }}</div>
						<div class="detnavcontextM">{{$uerstore.addjl(item.distance)}}m</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import {
	Toast,Dialog 
} from 'vant';
export default{
  data () {
    return {
      result: []//选中的内容
    }
  },
  props:['conList'],
  watch:{
	  result(e){
		  this.$emit('resultcheckbox',e);  //选中的内容
	  }
  },
  methods: {
	clickindex(e,name){
	    // 参数传递
		this.$emit('detalisid',e);  //detalisid传递媒介父级用于接收
	}
  },
  mounted () {
    // console.log('132')
  }
}
</script>

<style scoped>
	.detnavcon{
		width: 100%;
	}
	.detnavcontext{
		width: calc(100% - 74px);
		float: left;
		margin-left: 20px;
		text-align: left;
		position: relative;
	}
	.detnavcontextM{
		position: absolute;
		bottom: 0;
		right: 0;
		line-height: 27px;
		font-size: 12px;
	}
	.detnavcontexttit{
		line-height: 27px;
		height: 27px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.detnavcontextdex{
		width: 70%;
		line-height: 27px;
		font-size: 12px;
		color: #999999;
		height: 27px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.detnavconlogo {
		width: 54px;
		height: 54px;
		overflow: hidden;
		border-radius: 50%;
		float: left;
	}
	.detnavconlogo img{
		width: 100%;
		display: block;
	}
	.detnav{
		font-size:16px;
		font-weight: bold;
		text-align: left;
		position: relative;
		padding-left:12px;
		color: #333333;
		margin-bottom:20px;
	}
	.detnav::after{
		position: absolute;
		top: 0;
		left: 0;
		width: 4px;
		height: 21px;
		content: '';
		background:linear-gradient(313deg,#29bf5f 0%,#b2da9a 100%);
		border-radius:10px;
	}
	.detnavimg{
		width: 100%;
		max-height:180px;
		overflow: hidden;
		border-radius: 12px;
		margin-bottom: 12px;
	}
	.detnavimg img{
		width: 100%;
		display: block;
	}
	.INcontentConListCon{
		position: relative;
		width: calc(50% - 14px);
		padding: 0 7px;
		margin-bottom: 14px;
		float: left;
	}
	.INcontentConListConimg{
		width: 100%;
		border-radius:6px;
		padding-bottom: 100%;
		height: 0;
		overflow: hidden;
		position: relative;
		margin-bottom: 10px;
	}
	.INcontentConListConimg img{
		width: 100%;
		display: block;
	}
	.INcontentConListConJB{
		position: absolute;
		z-index: 3;
		top: 0;
		right: 7px;
		background-color: rgba(255,255,255,1);
		font-size:24px;
		width: 30px;
		height: 30px;
		border-radius:20px 0 0 20px;
	}
	.INcontentConListConText{
		font-size: 12px;
		color:#333;
		margin-top:6px;
	}
	.imghuis{
		filter:grayscale(100%);
	}
	.inwan{
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;
		width: 50% !important;
		margin: 25%;
	}
	.INstatecheckbox{
		height: 20px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
		width:100%;
		font-size: 14px;
	}
</style>



