<template>
	<div class="inOrder">
		<div class="inOrderOne">
			<div class="inOrderOneTIT" v-for="(item,index) in JGlist" :key="index">{{item.cname}}<span>￥{{item.price}}</span></div>
			<div class="inOrderOnemud">选择机构数量：<span>{{JGlist.length}}</span></div>
		</div>
		<div class="inOrderwto" v-show="informationXGstate==2?true:false">
			<div class="inOrderwtoqx" v-show="uidstate!=2?true:false"><span @click="informationXG(1)">取消修改</span></div>
			<van-field v-model="INname" label="姓名" type="textarea" placeholder="请输入学生姓名" rows="1" autosize />
			<div style="padding-left: 16px;">
				<van-radio-group v-model="radio" class="INbox">
					<van-radio checked-color="#FF6714" name="1" class="boxxz" style="margin-left: 25%;">男</van-radio>
					<van-radio checked-color="#FF6714" name="2" class="boxxz">女</van-radio>
				</van-radio-group>
			</div>
			<van-field v-model="INage" label="年龄" type="tel" placeholder="请输入学生年龄" rows="1" autosize />
			<van-field v-model="INtel"  center  clearable label="手机号" placeholder="请输入手机号">
			    <van-button v-show="show" slot="button" style="background-color:#FF6714;border:none;" size="small" type="primary" @click="oncode()">获取验证码</van-button>
				<van-button v-show="!show" slot="button" style="background-color:#FF6714;border:none;" size="small" type="primary">{{count}}</van-button>
			</van-field>
			<van-field v-model="INcode" label="验证码" maxlength="4" type="tel" placeholder="请输入验证码" rows="1" autosize />
		</div>
		<div class="inOrderthree" v-show="informationXGstate==1?true:false">
			<div><span>姓名：{{urexinxi.name}}</span><span class="inOrderthreeright">性别：{{urexinxi.sex==1?'男':'女'}}</span></div>
			<div><span>手机号：{{urexinxi.phone}}</span><span class="inOrderthreeright">年龄：{{urexinxi.age}}</span></div>
			<p class="inOrderthreebut" @click="informationXG(2)">修改</p>
		</div>
		<div class="inOrderBottom">
			<p @click="INbmok()">确认提交</p>
		</div>
	</div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';
	export default {
		data() {
			return {
				INname: '',
				INage: '',
				INtel: '',
				radio: '1',
				INcode:'',
				JGlist:[],
			    show: true,
			    count: '',
			    timer: null,
				informationXGstate:2,//修改显示2第一次，1以后
				uidstate:2,//第一次
				urexinxi:[],//已有的信息内容
				TJstate:2,//提交状态
			};
		},
		methods: {
			//信息修改
			informationXG(e){
				this.informationXGstate=e;
				this.TJstate = e;
			},
			//获取验证码
			oncode(){
				if(!(/^1[3456789]\d{9}$/.test(this.INtel))){
					Toast("手机号码有误，请重填");  
					return false; 
				}else{
					//发送验证码
					const TIME_COUNT = 60;
					 if (!this.timer) {
					   this.count = TIME_COUNT;
					   this.show = false;
					   this.timer = setInterval(() => {
					   if (this.count > 0 && this.count <= TIME_COUNT) {
						 this.count--;
						} else {
						 this.show = true;
						 clearInterval(this.timer);
						 this.timer = null;
						}
					   }, 1000)
					  }	
					this.codeajax();
				}
			},
			INbmok() {
				if (this.TJstate==1) {
					let ajaxlistcon={
						uid:localStorage.getItem('INcodeuid'),
						ids:this.$route.query.id,
						catType:this.$route.query.catType,
						type_Sub:this.TJstate
					};
					if (this.JGlist.length==6) {
						 this.JGtijiao(ajaxlistcon);
					} else{
						Dialog.confirm({
						  title: '温馨提示',
						  message: '您当前选择了'+this.JGlist.length +'家机构，是否确认您的选择？'
						}).then(() => {
						  // on confirm
						 this.JGtijiao(ajaxlistcon);
						}).catch(() => {
						  Toast("您选择了取消！"); 
						});
					}
				}else{
					let ajaxlistcon={
						uid:localStorage.getItem('INcodeuid'),
						ids:this.$route.query.id,
						catType:this.$route.query.catType,
						sname:this.INname,
						sex:this.radio,
						age:this.INage,
						phone:this.INtel,
						code:this.INcode,
						type_Sub:this.TJstate
					};
					//验证
					if (ajaxlistcon.sname=='') {
						return Toast("请填写姓名");  
					};
					if (ajaxlistcon.age==''|| isNaN(ajaxlistcon.age)) {
						return Toast("请正确填写年龄，如：6");  
					};
					if (ajaxlistcon.phone=='') {
						return Toast("请填写手机号");  
					};
					if (ajaxlistcon.code=='') {
						return Toast("请填写验证码");  
					};
					//手机号验证
					if(!(/^1[3456789]\d{9}$/.test(this.INtel))){
						return Toast("手机号码有误，请重填");  	
					};
					if (this.JGlist.length==6) {
						 this.JGtijiao(ajaxlistcon);
					} else{
						Dialog.confirm({
						  title: '温馨提示',
						  message: '您当前选择了'+this.JGlist.length +'家机构，是否确认您的选择？'
						}).then(() => {
						  // on confirm
						 this.JGtijiao(ajaxlistcon);
						}).catch(() => {
						  Toast("您选择了取消！"); 
						});
					}					
				}	
			},
			//机构提交
			JGtijiao(ajaxlistcon){
				Toast.loading({
				  message: '加载中...',
				  forbidClick: true
				});
				this.axios.post('addOrder',this.qs.stringify(ajaxlistcon)).then((response)=>{
				    // ...
					console.log('提交订单',response.data);
					Toast(response.data.msg);
					if (response.data.code==1000) {
						// this.$router.push({
						// 	path: '/inSuccess',
						// 	query: {
						// 		id: response.data.data.order_num,
						// 		mud:this.JGlist.length
						// 	}
						// });
						Toast.clear();
						this.$uerstore.store.commit("SuccessBAOM",99);//状态写入
						this.$router.push({path:'/'});
					};
				});
			},
			//机构列表
			JGajaxlist(){
				let dataS={
					ids:this.$route.query.id,
					id:this.$route.query.uid,
				};
				this.axios.post('getMemberData',this.qs.stringify(dataS)).then((response)=>{
				    // ...
					console.log('选择机构订单列表',response.data);
					this.JGlist = response.data.data.Member_data;
					this.uidstate = response.data.data.name_data.type;//当前状态时候是第一次
					this.informationXGstate = response.data.data.name_data.type;//第二次显示状态来来验证显示
					this.TJstate = response.data.data.name_data.type;//提交状态
					this.urexinxi = response.data.data.name_data;//信息内容
				})
			},
			//获取验证码
			codeajax(){
				let codees={
					phone:this.INtel
				};
				this.axios.post('alimsg',this.qs.stringify(codees)).then((response)=>{
				    // ...		
					Toast("手机验证码已发送，请注意查收"); 			
				})
			},
		},
		watch:{
			INname(e){
				localStorage.setItem('INnameStore',e);//缓存名字
			},
			radio(e){
				localStorage.setItem('radioStore',e);//缓存性别
			},
			INage(e){
				localStorage.setItem('INageStore',e);//缓存年龄
			},
			INtel(e){
				localStorage.setItem('INtelStore',e);//缓存手机号
			}
		},
		mounted() {
			this.JGajaxlist();
			// 数据缓存
			if (localStorage.getItem('INnameStore')=='' || localStorage.getItem('INnameStore')==null || localStorage.getItem('INnameStore')==undefined) {}else{this.INname = localStorage.getItem('INnameStore')}
			if (localStorage.getItem('radioStore')=='' || localStorage.getItem('radioStore')==null || localStorage.getItem('radioStore')==undefined) {}else{this.radio = localStorage.getItem('radioStore')}
			if (localStorage.getItem('INageStore')=='' || localStorage.getItem('INageStore')==null || localStorage.getItem('INageStore')==undefined) {}else{this.INage = localStorage.getItem('INageStore')}
			if (localStorage.getItem('INtelStore')=='' || localStorage.getItem('INtelStore')==null || localStorage.getItem('INtelStore')==undefined) {}else{this.INtel = localStorage.getItem('INtelStore')}
		}
	};
</script>

<style scoped>
	/* 取消修改 */
	.inOrderwtoqx{
		overflow: hidden;
	}
	.inOrderwtoqx span{
		font-size: 12px;
		display: block;
		float: right;
		background-color: #CCCCCC;
		height:30px;
		padding: 0 10px;
		border-radius: 20px;
		line-height: 30px;
	}
	/* 内容 */
	.inOrder {
		padding: 14px;
		width: calc(100% - 28px);
	}

	.inOrderOne {
		background-color: #FFFFFF;
		border-radius: 12px;
		padding: 20px;
		margin-bottom: 14px;
	}

	.inOrderOneTIT {
		font-size: 14px;
		margin-bottom: 16px;
		text-align: left;
		overflow: hidden;
		color: #333333;
	}

	.inOrderOneTIT span {
		float: right;
		color: #D42E00;
	}

	.inOrderOnemud {
		font-size: 12px;
		color: #ccc;
		text-align: left;
	}

	.inOrderOnemud span {
		color: #E14901;
	}

	/* 第二部分 */
	.inOrderwto {
		background-color: #FFFFFF;
		border-radius: 12px;
		padding: 20px;
		margin-bottom: 14px;
	}

	.INbox {
		overflow: hidden;
		border-bottom: 1px solid #ebedf0;
	}

	.boxxz {
		float: left;
		margin: 12px 18px;
		font-size: 14px;
	}
	/* 第三部分 */
	.inOrderthree {
		background-color: #FFFFFF;
		border-radius: 12px;
		padding: 20px;
		margin-bottom: 14px;
		text-align: left;
		font-size: 0.875rem;
	}
	.inOrderthree div{
		margin: 10px 0;
	}
	.inOrderthreeright{
		float: right;
	}
	.inOrderthreebut{
		text-align: center;
		line-height: 2rem;
		margin-top: 1.25rem;
		color: #E14901;
		margin-bottom: 0;
	}
	/* 底部 */
	.inOrderBottom {
		width: 100%;
		height: 54px;
		background: #FFFFFF;
		position: fixed;
		bottom: 0;
		left: 0;
		color: #fff;
	}

	.inOrderBottom p {
		width: 80%;
		height: 44px;
		margin: 5px auto;
		background: linear-gradient(90deg, rgba(255, 160, 45, 1) 0%, rgba(255, 103, 20, 1) 100%);
		line-height: 44px;
		border-radius: 44px;
	}
	
</style>



