<template>
  <div class="flastcon">
	  <div style="position: relative;">
			<div class="yuanIN" v-show="carmud!=0">{{carmud}}</div>
		 <img @click="incar()" class="imgbox" src="../../assets/icon/icar.png" alt=""> 
	  </div>
      <img @click="fenxiang()" class="imgbox imgboxdd" src="../../assets/icon/ishare.png" alt="">
	  <!-- <img @click="JGRZHU()" class="imgbox" src="../../assets/fx119.png" alt=""> -->
	  <!-- 遮罩 -->
	  <div v-show="sharebgstate" class="sharebg" @click="sharebg()">
		  <img style="float: right;margin-top: 20px;" src="../../assets/jt.png" alt="">
	  </div>
	  <!-- 机构入住 -->
	  <van-action-sheet v-model="JGshow" title="机构加盟">
			<div style="padding: 24px 40px 24px 24px;">
				<van-field v-model="JGname" label="机构名称" left-icon="home-o" placeholder="请输入" />
				<van-field v-model="INname" label="联系人" left-icon="contact" placeholder="请输入" />
				<van-field v-model="INtel" label="手机号码" left-icon="phone-o" placeholder="请输入" />
				<van-button type="primary" color="#fe892b" size="large" class="butINJG" @click="INJGbutton()">立即加盟</van-button>
			</div>		  
	  </van-action-sheet>
  </div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';
	export default {
	  data() {
	    return {
			sharebgstate:false,
			carmud:'0',
			JGshow:false,
			JGname:'',
			INname:'',
			INtel:''
	    };
	  },
	  props:['carlenght'],
	  watch:{
		  carlenght(){
			 this.carajaxlist(); 
		  },
	  },
	  methods: {
			incar(){
				this.$router.push({path:'/car'});
			},
			fenxiang(){
				window.location.href = 'https://yxhd.insooner.com/dista?urluid='+localStorage.getItem('INcodeuid');
			},
			sharebg(){
				this.sharebgstate=false
			},
			//购物车
			carajaxlist(){
				let dataS={
					id:localStorage.getItem('INcodeuid')
				};
				this.axios.post('getCat',this.qs.stringify(dataS)).then((response)=>{
				    // ...
					console.log('购物车长度数据',response.data.data);
					this.carmud = response.data.data.res.length
				})
			},
			//机构入住
			JGRZHU(){
				this.JGshow=true;
			},
			// 提交
			INJGbutton(){
				if(!(/^1[3456789]\d{9}$/.test(this.INtel))){
					Toast("手机号码有误，请重填");  
					return false; 
				}else{
					let dataJGOobj={
						name:this.INname,
						cname:this.JGname,
						phone:this.INtel,
						uid:localStorage.getItem('INcodeuid')
					};
					this.axios.post('addClient',this.qs.stringify(dataJGOobj)).then((response)=>{
						// ...
						console.log('机构入驻提交',response.data.data);
						this.JGshow=false;
						Toast("感谢您对因学的信赖，我们将在2个工作日内与您联系，请您耐心等待！\n\n如果您迫切着急：请您拨打4000060996与我们联系。");
					})
				}
			}
	  },
	  mounted() {
		  this.carajaxlist()
	  }
	};
</script>

<style scoped>
	.yuanIN{
		position: absolute;
		top: 0;
		right: 4px; 
		width: 20px;
		height: 20px;
		border-radius: 50%;
		background-color: #ee0a24;
		line-height: 20px;
		text-align: center;
		font-size: 12px;
		color: #fff;
	}
  .flastcon{
    position:fixed;
    bottom:120px;
    right: 24px;
    width: 54px;
    z-index:2;
  }
  .imgbox{
    width: 54px;
    height: 54px;
  }
  .sharebg{
	  background-color: rgba(0,0,0,0.5);
	  width: 100%;
	  height: 100%;
	  position: fixed;
	  top: 0;
	  left: 0;
  }
  .imgboxdd{
  	  -webkit-animation: seconddiv 2s infinite linear;
  	  background: transparent;	
  }
  
  @keyframes seconddiv{
	  0% {transform: scale(1,1);}
	  10% {transform: scale(1,1);}
	  25% {transform: scale(1.2,1.2);}
	  50% {transform: scale(1,1);}
	  70% {transform: scale(1.2,1.2);}
	  100% {transform: scale(1,1);}
  }
  /* 提交按钮 */
  .butINJG{
	  margin: 40px 0 20px 0;
	  
  }
</style>



