<template>
	<div class="user">
		<div class="usercon">
			<div class="userconlogo">
				<img src="../../assets/icon/icar.png" alt="">
			</div>
			<div class="userconname">我是逆行者</div>
			<div class="usercontext">编辑</div>
		</div>
		<!-- 内容 -->
		  <van-row class="usernav">
			<van-col span="8" class="usernav_st">
				<div>1</div>
				<p>积分</p>
			</van-col>
			<van-col span="8" class="usernav_st">
				<div>1</div>
				<p>课程</p>
			</van-col>
			<van-col span="8" class="usernav_st">
				<div>8</div>
				<p>年龄</p>
			</van-col>
		  </van-row>	
		<!-- banner -->
		<img style="width: 100%;display:block;" v-lazy="bannerimg">
		<!-- 我报名的课程 -->
		<div class="briefJJ">
			<div class="detnav">已报课程</div>
			<div class="briefJJimg" v-for="(item,index) in 4" :key="index" >
				<van-card thumb="https://img.yzcdn.cn/vant/ipad.jpeg" @click-thumb="detJG()">
				  <div slot="tags" style="position: relative;">
					<div class="userrem" @click="remuser()">删除</div>
				    <div class="briefJJtit">我要选我要选课我要选课我要选课课</div>
					<div class="briefJJtitru" style="color: #999999;">我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课我要选课</div>
				  </div>
				</van-card>
			</div>
		</div>
	</div>
</template>

<script>
	import { Dialog } from 'vant';
	export default {
	  data() {
	    return {
	      bannerimg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/yy.png'
	    };
	  },
	  methods:{
		 detJG(){
			 console.log(123)
		 },
		 remuser(){
			Dialog.confirm({
			  title: '删除课程',
			  message: '是否删除当前选择的课程'
			}).then(() => {
			  // on confirm
			}).catch(() => {
			  // on cancel
			});  
		 }
	  }
	};
</script>

<style scoped>
	.userrem{
		position: absolute;
		right: 0;
		top: 0;
		font-size: 14px;
		color: #E14901;
		line-height: 27px;
	}
	/* 列表 */
	/* 简介 */
	.briefJJimg{
		text-align: left;
		margin-bottom: 20px;
	}
	.briefJJtitru{
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
		font-size: 14px;
	}
	.briefJJtit{
		width: calc(100% - 50px);
		line-height: 27px;
		height: 27px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 14px;
	}
	.briefJJ{
		padding: 20px 14px;
		background-color: #fff;
		margin-bottom: 10px;
		text-align: left;
	}
	.briefJJTit{
		height: 44px;
		text-align: left;
		line-height: 44px;
		color:rgba(51,51,51,1);
		font-weight: bold;
	}
	.briefJJTit img{
		width: 22px;
		height: 22px;
		float: left;
		margin: 11px 10px 0 0;
	}
	.briefJJTEXT{
		font-size: 14px;
		color: #736E6B;
		text-align: left;
		margin-bottom: 60px;
		padding-bottom:20px;
	}
	.detnav{
		font-size:16px;
		font-weight: bold;
		text-align: left;
		position: relative;
		padding-left:12px;
		color: #333333;
		margin-bottom:20px;
	}
	.detnav::after{
		position: absolute;
		top: 0;
		left: 0;
		width: 4px;
		height: 21px;
		content: '';
		background:linear-gradient(313deg,#29bf5f 0%,#b2da9a 100%);
		border-radius:10px;
	}
	.detnavimg{
		width: 100%;
		max-height:180px;
		overflow: hidden;
		border-radius: 12px;
		margin-bottom: 12px;
	}
	.detnavimg img{
		width: 100%;
		display: block;
	}
	/* 上半部分 */
	.usernav{
		margin: 15px 0;
	}
	.usernav_st div{
		font-size: 16px;
		color: #333333;
	}
	.usernav_st p{
		margin: 6px 0;
		color: #999;
		font-size: 14px;
	}
	.user{
		width:calc(100% - 30px);
		height:calc(100% - 30px);
		padding:15px;
	}
	.usercon{
		width: 100%;
		overflow: hidden;
	}
	.userconlogo{
		float: left;
		width: 60px;
		height: 60px;
		margin-right: 15px;
	}
	.userconlogo img{
		width: 100%;
		display: block;
	}
	.userconname{
		font-size:16px;
		color: #333333;
		width: calc(100% - 150px);
		float: left;
		line-height: 60px;
		text-align: left;
	}
	.usercontext{
		float: right;
		border: 1px #29BF5F solid;
		font-size: 12px;
		color: #29BF5F;
		padding: 2px 12px;
		border-radius: 12px;
		margin-top: 19px;
	}
</style>



