<template>
	<div class="brief">
		<!-- 顶部内容 -->
		<div style="padding: 12px;overflow: hidden;background-color: #fff;">
			<div  class="detnavimg">
				<img v-lazy="conlist.img" >
			</div>
			<div class="detnavcon">
				<div class="detnavcontext">
					<div class="detnavcontexttit">{{conlist.cname}}</div>
					<div class="detnavcontextdex">{{conlist.remarks}}</div>
				</div>
			</div>
		</div>
		<!-- 内容 -->
		<div class="briefTit" style="position: relative;">
			<div class="briefaddes" @click="addDQ()"><van-icon name="location-o" style="font-size: 16px;padding-right:6px;" />地址：<span>{{conlist.address}}</span></div>
			<div class="briefTittel" @click="callPhone(conlist.phone)">
				<van-icon :name="tellog" size="20px" />
				<div style="font-size: 12px;">咨询</div>
			</div>
		</div>
		<!-- 课程列表 -->
		<div class="briefJJ">
			<div class="detnav">课程介绍</div>
			<div class="briefJJimg" style="padding:15px 0;">
				<div style="padding:0 10px 12px 10px;" v-for="(item,index) in conlist.course" :key="index">
					<div style="font-size: 14px; color:#333;text-align: left;text-align: justify;letter-spacing:2px;line-height: 20px;">{{item.title}}</div>
				</div>
			</div>
		</div>
		<div class="briefbuttom">
			<van-goods-action style="bottom: 4px;">
				<div class="briefbuttomcar">
					<div class="yuanIN" v-show="carlenght!=0">{{carlenght}}</div>
					<img  @click="carcont()" src="../../assets/icon/icar.png" alt="">
				</div>
			  <van-goods-action-button style="background:#7abd9c;"  type="warning" text="加入购物车" @click="onClickButton"/>
			  <van-goods-action-button style="background:#4eb180;"  type="danger" text="我要报名" @click="okselection()"/>
			</van-goods-action>
		</div>
		<!-- 弹窗 -->
		<van-popup v-model="Popupshow" round>
			<div class="Popupshowcon">
			   <h5>我要报名</h5>
			   <van-field v-model="Popuptext.name" label="姓名" placeholder="请输入姓名"/>
			   <van-field v-model="Popuptext.age" label="年龄" placeholder="请输入年龄"/>
			   <van-field v-model="Popuptext.tel" type="tel" label="手机号" placeholder="请输入手机号"/>
			   <div style="font-size: 12px;margin:15px 0;">我们通过电话与您联系，请保证电话真实畅通~</div>
			   <van-button size="large" type="primary" block color="#4eb180" @click="baomincli()">确认报名</van-button>
			</div>
		</van-popup>
	</div>
</template>

<script>
	import { Toast } from 'vant';
	export default {
		data() {
			return {
				// 验证信息
				Popuptext:{
					name:'',
					age:'',
					tel:''
				},
				conlist:[],
				mud:'',//剩余名额
				mudes:'',//剩余数量
				JGLon:'',//经度
				JGlat:'',//纬度
				carlenght:'',
				orderStatus:'1',//购买状态
				tellog:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/phone.png',//电话图标
				Popupshow:false
			}
		},
	  methods: {
		//报名
		baomincli(){
			this.Popupshow = false;
		},
		//拨打电话
		callPhone(e){
			window.location.href = 'tel://'+e
		},
		addDQ(){
			// 地图调用
			window.location.href="https://apis.map.qq.com/uri/v1/routeplan?type=walk&from=我的位置&fromcoord="+localStorage.getItem('INlatitude')+","+localStorage.getItem('INlongitude')+"&to=机构位置&tocoord="+this.JGlat+","+this.JGLon+"&policy=3&referer=AZ6BZ-G5WWO-UGOWQ-STA4N-NQG72-2TB4X"
		},
		Conajax(){
			let dataM={
				id:this.$route.query.id,
				latitude:localStorage.getItem('INlatitude'),
				longitude:localStorage.getItem('INlongitude'),
				uid:localStorage.getItem('INcodeuid')
			};
			this.axios.post('getMemberInfo',this.qs.stringify(dataM)).then((response)=>{
			    // ...
				this.conlist = response.data.data;
				this.mud ="剩余名额："+ response.data.data.re_num + "人";
				this.mudes = response.data.data.re_num;
				this.JGLon = response.data.data.longitude;
				this.JGlat = response.data.data.latitude;
				this.orderStatus =response.data.data.order_status;
				console.log('机构详情', response.data.data);
			})
		},
	    okselection() {
			if (this.mudes<=0) {
				Toast.fail('当前机构课程报名人数已满');
			} else{
				if (this.orderStatus=='1') {
					Toast.fail('您在当前的机构课程已报过名！');
				} else{
					 this.Popupshow = true;
				}
			}
	    },
		// 报名
	    onClickButton() {
	      if (this.mudes<=0) {
	      	Toast.fail('当前机构报名人数已满');
	      } else{
			this.carJR();
	      }
	    },
		//加入购物车
		carJR(){
			let dataS={
				mid:this.$route.query.id,
				uid:localStorage.getItem('INcodeuid')
			};
			this.axios.post('addCat',this.qs.stringify(dataS)).then((response)=>{
			    // ...
				Toast.success(response.data.msg);
				this.carajaxlist();
			})
		},
		//购物车数量
		carajaxlist(){
			let dataS={
				id:localStorage.getItem('INcodeuid')
			};
			this.axios.post('getCat',this.qs.stringify(dataS)).then((response)=>{
			    // ...
				console.log('购物车长度数据',response.data.data);
				this.carlenght = response.data.data.res.length;
			})
		},
		//进入购物车
		carcont(){
			this.$router.push({path:'/car'});
		},
		fenxiangbrief(){
			window.location.href = 'https://yxhd.insooner.com/dista?urluid='+localStorage.getItem('INcodeuid');
		},
		//分享用户授权
		shareures(){
			const INUserID = this.$uerstore.store.state.BDuidTHIS;//传入用户id
			const INUserMID = this.$uerstore.store.state.GJmidTHIS;//传入机构id
			if (INUserID==undefined&&INUserMID==undefined) return;
			if (INUserID==undefined || INUserID==null || INUserID=='') {
				let thisdata = {
					id:localStorage.getItem('INcodeuid'),
					inviteid:'',
					latitude:localStorage.getItem('INlatitude'),
					longitude:localStorage.getItem('INlongitude'),
					mid:INUserMID
				};
				this.axios.post('setDistribution',this.qs.stringify(thisdata)).then((response)=>{
				    // ...
					console.log('分享信息获取',response);
				});
			}else if(INUserMID==undefined || INUserMID==null || INUserMID==''){
				let thisdata = {
					id:localStorage.getItem('INcodeuid'),
					inviteid:INUserID,
					latitude:localStorage.getItem('INlatitude'),
					longitude:localStorage.getItem('INlongitude'),
					mid:''
				};
				this.axios.post('setDistribution',this.qs.stringify(thisdata)).then((response)=>{
				    // ...
					console.log('分享信息获取',response);
				});	
			}
			
		}
	  },
	  mounted() {
	  	this.Conajax();
		this.carajaxlist();
		this.shareures();
	  },
	}
</script>

<style scoped>
	/* 弹窗 */
	.Popupshowcon{
		width:280px;
		background-color: #F7F7F7;
		border-radius: 5px;
		overflow: hidden;
	}
	
	.briefTittel{
		width: 60px;
		position: absolute;
		right: 0;
		top:2px;
	}
	.brief_bq{
		width: 100%;
	}
	.brief_bq p{
		float:left;
		border: 1px #41b180 solid;
		border-radius: 4px;
		font-size: 12px;
		color: #41b180;
		margin-right: 5px;
		padding: 2px 5px;
	}
	.detnavcon{
		width: 100%;
	}
	.detnavcontext{
		width:100%;
		float: left;
		text-align: left;
		position: relative;
	}
	.detnavcontextM{
		position: absolute;
		bottom: 0;
		right: 0;
		line-height: 27px;
		font-size: 12px;
	}
	.detnavcontexttit{
		line-height: 27px;
		height: 27px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.detnavcontextdex{
		width: 70%;
		line-height: 27px;
		font-size: 12px;
		color: #999999;
		height: 27px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.detnavconlogo {
		width: 54px;
		height: 54px;
		overflow: hidden;
		border-radius: 50%;
		float: left;
	}
	.detnavconlogo img{
		width: 100%;
		display: block;
	}
	.detnav{
		font-size:16px;
		font-weight: bold;
		text-align: left;
		position: relative;
		padding-left:12px;
		color: #333333;
		margin-bottom:20px;
	}
	.detnav::after{
		position: absolute;
		top: 0;
		left: 0;
		width: 4px;
		height: 21px;
		content: '';
		background:linear-gradient(313deg,#29bf5f 0%,#b2da9a 100%);
		border-radius:10px;
	}
	.detnavimg{
		width: 100%;
		max-height:180px;
		overflow: hidden;
		border-radius: 12px;
		margin-bottom: 12px;
	}
	.detnavimg img{
		width: 100%;
		display: block;
	}
	
	
	
	.yuanIN{
		position: absolute;
		top: 0;
		right: 4px; 
		width: 20px;
		height: 20px;
		border-radius: 50%;
		background-color: #ee0a24;
		line-height: 20px;
		text-align: center;
		font-size: 12px;
		color: #fff;
	}
	.brief{
		width: 100%;
		background-color: #FAFAF9;
		padding-bottom: 44px;
	}

	.briefTit{
		padding: 0 14px;
		background-color: #fff;
		margin: 10px 0;
	}
	.briefaddes{
		width: 100%;
		height:45px;
		font-size: 13px;
		text-align: left;
		color: #333;
		line-height: 45px;
	}
	.briefaddes span{
		color: #666;
	}

	/* 简介 */
	.briefJJ{
		padding: 20px 14px;
		background-color: #fff;
		margin-bottom: 10px;
	}
	.briefJJTit{
		height: 44px;
		text-align: left;
		line-height: 44px;
		color:rgba(51,51,51,1);
		font-weight: bold;
	}
	.briefJJTit img{
		width: 22px;
		height: 22px;
		float: left;
		margin: 11px 10px 0 0;
	}
	.briefJJTEXT{
		font-size: 14px;
		color: #736E6B;
		text-align: left;
		margin-bottom: 60px;
		padding-bottom:20px;
	}
	/* 图片 */
	.briefJJimg{
		/* width: 100%; */
	}
	.briefbuttom{
		width: 100%;
		height: 54px;
		background-color: #fff;
		position: fixed;
		bottom: 0;
		left: 0;
	}
	.briefbuttomcar{
		width:45px;
		height: 45px;
		float: left;
		margin-left:8px;
		position: relative;
	}
	.briefbuttomcar img{
		width: 100%;
		display: block;
	}
	/* 分享 */
	.flastcon{
	  position:fixed;
	  bottom: 200px;
	  right: 24px;
	  width: 54px;
	  z-index:2;
	}
	.imgbox{
	  width: 54px;
	  height: 54px;
	}
	.sharebg{
		  background-color: rgba(0,0,0,0.5);
		  width: 100%;
		  height: 100%;
		  position: fixed;
		  top: 0;
		  left: 0;
	}
	.imgboxdd{
		  -webkit-animation: seconddiv 2s infinite linear;
		  background: transparent;	
	}
	
	@keyframes seconddiv{
		0% {transform: scale(1,1);}
		10% {transform: scale(1,1);}
		25% {transform: scale(1.2,1.2);}
		50% {transform: scale(1,1);}
		70% {transform: scale(1.2,1.2);}
		100% {transform: scale(1,1);}
	}
</style>
<style>
	.briefJJimg img{
		width: 100%;
		height: auto;
		display: block;
	}
</style>


