<template>
	<div style="width: 100%;height: 100%;background:linear-gradient(-77deg,rgba(255,236,183,1) 0%,rgba(255,192,2,1) 100%);">	
		<div class="sharebg">
			<img class="sharebgimg" src="../../assets/bgmoeny.png" alt="">
			<div class="sharebgcon">
				<div class="sharebgheader" style="position: relative;">
					<img src="../../assets/logoBAIS.png" alt="">
					<div class="sharebgSM" @click="inRules()">活动规则</div>
				</div>
				<div class="sharebgconbb" style="position: relative;">	
					<div class="sharebgtop">
						<div style="padding: 0 27px 30px 27px;">
						  <div style="font-size: 14px;padding: 20px 0;color: #333333;">您当前的奖励倍数是<span style="color:#FF3007;">1</span>倍</div>
						  <!-- 进度条 -->
						  <div class="sharebgconbbJD"></div>
						  <!-- 文字 -->
						  <div style="height:40px;font-size: 10px;padding: 0 12px;">
							  <div style="float: left;line-height: 40px;">累计分享</div>
							  <div style="float: right;line-height: 40px;">0次</div>
						  </div>
						  <div class="sharebgconbbtext">分享到群里或好友，其他人通过选课报名，您将获得<span style="color:#FF3007;">现金红包</span>，上不封顶！</div>
						  <div class="sharebgconbbbuttom" @click="fenxiang()">喊好友来助力</div>
						</div>
					</div>
					<div class="sharebgZ">
						<img src="../../assets/dgZONGJIAN.png" alt="">
					</div>
					<div class="sharebgbottom">
						<div style="color: #BF9D6D;font-size: 12px;">满100元就可提现哦~</div>
						<div style="color: #000; font-size: 12px;line-height: 40px;">您已累积获得奖励 <span style="color:#BF9D6D;">108元</span></div>
						<div class="sharebgbottomBOT">我要提现</div>
						<div style="color: #BF9D6D;font-size: 12px;margin-top: 40px;">分享给更多好友获得更多机会哦~</div>
					</div>
				</div>
			</div>
		</div>
		<!-- 遮罩 -->
		<div v-show="sharebgstate" class="sharebgzhao" @click="sharebg()">
			<img style="float: right;margin-top: 20px;" src="../../assets/jt.png" alt="">
		</div>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
			sharebgstate:false
	    };
	  },
	  methods: {
			fenxiang(){
				// this.$uerstore.store.state.ahare = 1;
				// this.$router.go(-1);//返回上一层
			},
			sharebg(){
				this.sharebgstate=false
			},
			// 微信分享
			inRules(){
				this.$router.push({path:'/Rules'});
			}			
	  },
	  mounted() {
	  }
	};
</script>

<style scoped>
	.sharebg{
		width: 100%;
		background:linear-gradient(-77deg,rgba(255,236,183,1) 0%,rgba(255,192,2,1) 100%);
		position: relative;
	}
	.sharebgimg{
		width: 100%;
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 0;
	}
	.sharebgcon{
		padding: 0 14px;
	}
	.sharebgheader{
		width: 100%;
		height:40px;
	}
	.sharebgheader img{
		height: 26px;
		float: left;
		margin-top: 6px;
	}
	.sharebgSM{
		width: 75px;
		height: 20px;
		text-align: center;
		font-size: 12px;
		background-color: rgba(255,255,255,0.7);
		float: right;
		margin: 10px -14px 0 0;
		line-height: 20px;
		border-radius: 20px 0 0 20px;
		color: #333;
	}
/* 中间板块 */
    .sharebgconbb{
		width: 100%;
	}
	.sharebgtop{
		background-color: #FFFFFF;
		border-radius: 20px 20px 0 0;
	}
	.sharebgconbbJD{
		width: 100%;
		height:12px;
		background:rgba(254,247,229,1);
		border-radius: 12px;
	}
	.sharebgconbbtext{
		padding: 50px 0; 
		font-size: 14px;
		color: #333;
	}
	.sharebgconbbbuttom{
		width:100%;
		height:50px;
		background:linear-gradient(0deg,rgba(249,118,46,1),rgba(255,56,103,1));
		border-radius:50px;
		line-height: 50px;
		color:#fff;
		font-size: 18px;
	}
	.sharebgZ img{
		width: 100%;
		display: block;
	}
	.sharebgbottom{
		width: 100%;
		background-color: #fff;
		padding-bottom: 60px;
		border-radius: 0 0 20px 20px;
	}
	.sharebgbottomBOT{
		width:125px;
		height:42px;
		background:linear-gradient(0deg,rgba(207,169,114,1),rgba(229,188,128,1));
		border-radius:42px;
		line-height: 42px;
		color: #FFF;
		margin: 10px auto 0 auto;
	}
	/* 遮罩 */
	.sharebgzhao{
		  background-color: rgba(0,0,0,0.5);
		  width: 100%;
		  height: 100%;
		  position: fixed;
		  top: 0;
		  left: 0;
	}
</style>



