<template>
	<div class="inmoreCon">
		<!-- 搜索 -->
		<div style="position: fixed;top:0;left: 0;width: 100%; z-index: 99;">
			<van-search v-model="value" placeholder="请输入搜索关键词" show-action  shape="round" @search="onSearch">
			  <div slot="action" @click="onSearch">搜索</div>
			</van-search>
			<van-dropdown-menu style="width:50%;float: left;line-height: 50px;">
			   <div style="width: 100%;height: 50px;text-align: center;" :class="navstateColor==0?'navColor':''" @click="thisquan()">附近</div>
			</van-dropdown-menu>
			<van-dropdown-menu style="width: 50%;float: left;" :class="navstateColor==1?'navColor':''" active-color="#FDB53D">
			  <van-dropdown-item v-model="value1" :options="option1"/>
			</van-dropdown-menu>
		</div>
		<!-- 列表内容 -->
		<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad" style="margin-top: 104px; background-color: #fff;">
		  <van-card  :desc="item.remarks" :title="item.cname" :thumb="item.img" v-for="(item,index) in list" :key="index" @click="THISJG(item.uid)">
		  <div slot="tags" style="line-height: 40px;position: relative;">
			  <span plain type="danger"><span style="font-size: 16px;color: #D42E00;">{{item.re_num}}</span>剩余名额</span>
			  <span plain type="danger">| {{$uerstore.addjl(item.distance)}}m</span>
			  <img v-show="item.re_num==0" style="width: 80px; height:80px;position:absolute; right:10px;top:-40px;z-index:3;" src="../../assets/wanH.png" alt="">
		  </div>
		  </van-card>
		</van-list>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  value:'',//输入内容
	      value1: '',//选中的1
		  value2:'',
		  option2: [
		          { text: '全部', value: 'a' },
		        ],
	      option1: this.Aajaxnav(this.$uerstore.INnav1),//选择1
		  list: [],//内容
		  loading: false,
		  finished: false,
		  inpage:0,
		  navstateColor:1
	    }
	  },
	  watch:{
		value1(e){
			this.inpage=0;
			this.finished = false;
			this.list=[];
			this.$uerstore.store.state.INnav1state.id = e;
			this.JGList();
			if (e!='') {
			  this.navstateColor = 1;	
			};
		}
	  },
	  methods: {
		  //全部
		  thisquan(){
			  this.value1 = '';	 
			  this.navstateColor = 0;	
		  },
		  //搜索信息
		  onSearch(e){
			  this.inpage=0;
			  this.finished = false;
			  this.list=[];
			  this.JGList(); 
		  },
		onLoad() {
		  // 异步更新数据
		  setTimeout(() => {
			 this.JGList();
			// 加载状态结束
			this.loading = false;
		  }, 500);
		},
		//机构列表
		JGList(){
			this.inpage++;
			let that = this;
			let thisdata = {
				latitude:localStorage.getItem('INlatitude'),
				longitude:localStorage.getItem('INlongitude'),
				cid :this.value1,
				// aid:this.value2,
				page:this.inpage,
				cname:this.value
			};
			this.axios.post('getMember',this.qs.stringify(thisdata)).then((response)=>{
			    // ...
				if (response.data.data=="") {
					return this.finished = true;
				} else{
					that.list = that.list.concat(response.data.data);
					// console.log('更多',this.list);
				};
			})
		},
		// A导航请求
		Aajaxnav(e){
			let that = this;
			let objthisthat=[{"text":'全部',"value":''}];
			if (e==undefined) {
				this.$router.go(-1);
			};
			e.forEach(function (c) {
				let objthis = {};
				objthis.text=c.name;
				objthis.value=c.id;
			    objthisthat.push(objthis);     
			});
			return objthisthat
		},
	   THISJG(E){
		   this.$router.push({path:'/brief',query:{id:E}});
		   this.$uerstore.store.state.inputname = this.value;
	   }
	  },
	  mounted() {
		  this.value = this.$uerstore.store.state.inputname;
		 if (this.$uerstore.store.state.INnav1state.id!= '') {
		 	this.value1 = this.$uerstore.store.state.INnav1state.id;//存入vuexid
		 }else{
			this.value1 = '';//默认状态
		 };
	  }
	};
</script>

<style scoped>
	.inmoreCon{
		width: 100%;
		height: 100%;
		text-align: left;
	}
	.van-tag--danger{
		background-color: rgba(0,0,0,0.5);
	}
	.van-card__title{
		font-size: 17px;
		line-height: 26px;
		max-height:26px;
	}
	.navColor{
		color:#FDB53D !important;
	}
</style>
<style>
	.inmoreCon .van-dropdown-menu__title{
		color: inherit;
	}
</style>



