<template>
	<div class="inmoreCon">
		<!-- 搜索 -->
		<!-- 列表内容 -->
		<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad" style=" background-color: #fff;">
		  <van-card  :title="item.cname" :thumb="item.img" v-for="(item,index) in list" :key="index" @click="THISJG(item.mid)">
			  <div slot="tags" style="line-height:20px;position: relative;">
				  <span plain type="danger">学生：{{item.sname}}</span>&nbsp;&nbsp;&nbsp;&nbsp;
				  <span plain type="danger">年龄：{{item.age}}</span>
				  <div>
					  <span plain type="danger">联系电话：{{item.phone}}</span>
				  </div>
				  <div>
					  <span plain type="danger">报名时间：{{$uerstore.timeZH(item.create_time)}}</span>
				  </div>
			  </div>
		  </van-card>
		</van-list>
	</div>
</template>

<script>
	export default {
	  data() {
	    return {
		  list: [],//内容
		  loading: false,
		  finished: false,
		  inpage:0
	    }
	  },
	  methods: {
		onLoad() {
		  // 异步更新数据
		  setTimeout(() => {
			 this.JGList();
			// 加载状态结束
			this.loading = false;
		  }, 500);
		},
		//机构列表
		JGList(){
			this.inpage++;
			let that = this;
			let thisdata = {
				id :localStorage.getItem('INcodeuid'),
				page:this.inpage,
			};
			this.axios.post('getOrder',this.qs.stringify(thisdata)).then((response)=>{
			    // ...
				if (response.data.data=="") {
					return this.finished = true;
				} else{
					that.list = that.list.concat(response.data.data);
					console.log('更多',this.list);
				};
			})
		},
	   THISJG(E){
		   this.$router.push({path:'/brief',query:{id:E}});
		   this.$uerstore.store.state.inputname = this.value;
	   }
	  }
	};
</script>

<style scoped>
	.inmoreCon{
		width: 100%;
		height: 100%;
		text-align: left;
		margin-top: 20px;
	}
	.van-tag--danger{
		background-color: rgba(0,0,0,0.5);
	}
	.van-card__title{
		font-size: 17px;
		line-height: 26px;
		max-height:26px;
	}
</style>



