<template>
  <div id="app">
	   <transition :name="transitionName"> 
	      <!-- <keep-alive> -->
             <router-view/><!-- 缓存页面 -->
		  <!-- </keep-alive> -->
	   </transition>   
  </div>
</template>

<script>
import {commonShare, shareTitle, shareUrl, shareImg, shareDesc} from "./USERJS/share";
//引入微信
import wx from 'weixin-js-sdk'
export default {
	data() {
	  return {
		transitionName:''
	  };
	},
	created () {
		//授权
	    // this.getCode();
		//定位
		this.getMyLocation();
	},
  methods:{
		//授权登录
		getCode () { // 非静默授权，第一次有弹框
			if (localStorage.getItem('INcodeuid')==undefined || localStorage.getItem('INcodeuid')==null ||localStorage.getItem('INcodeuid')=='') {
				const code = this.$uerstore.getUrlParam("code") // 截取路径中的code，如果没有就去微信授权，如果已经获取到了就直接传code给后台获取openId
				const local = window.location.href
				if (code == null || code === '') {
					window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + 'wxf47f6fd95e94366e' + '&redirect_uri=' + encodeURIComponent(local) + '&response_type=code&scope=snsapi_userinfo&state=1#wechat_redirect'
				} else {
					this.getOpenId(code) //把code传给后台获取用户信息
				}
			}else{
				//验证是否是有效登录
				let YZUID = {
					id:localStorage.getItem('INcodeuid')
				};
				this.axios.post('verId',this.qs.stringify(YZUID)).then((response)=>{
				    // ...	
					//授权失败重新访问页面
					if (response.data.code!=1000) {
						//重新访问页面
						localStorage.removeItem('INcodeuid');
						window.location.href = 'https://yxhd.insooner.com/dist';
					}else{
						//直接跳转指定页面
						this.INpagethis();
						commonShare('因学周年庆 百万豪礼大放送','https://yxhd.insooner.com/dist?urluid='+localStorage.getItem('INcodeuid'),'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/80.jpg','价值4800元超值大礼包，限时免费领！');
					}	
				})
			}
		},
		getOpenId (code) { // 通过code获取 openId等用户信息，/api/user/wechat/login 为后台接口
			let _this = this
			let thisdatacode = {
				code:code
			};
			this.axios.post('vx',this.qs.stringify(thisdatacode)).then((response)=>{
			    // ...	
				//授权失败重新访问页面
				if (response.data.code!=1000) {
					//重新访问页面
					window.location.href = 'https://yxhd.insooner.com/dist';
				}else{
					localStorage.setItem('INcodeuid',response.data.data.id);
					//直接跳转指定页面
					this.INpagethis();
					_this.$uerstore.store.commit("GZHcode",response.data.data.type);//公众号
					console.log(response);
					commonShare('因学周年庆 百万豪礼大放送','https://yxhd.insooner.com/dist?urluid='+response.data.data.id,'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/80.jpg','价值4800元超值大礼包，限时免费领！');
				}	
			});
		},
		//获取微信的授权信息定位
		getMyLocation() {
			const that =this;
			//经纬度转换
			const latng = this.$route.query.latng;
			const addr = this.$route.query.city;//地图选点返回内容
			// console.log(this.$route.query);
			if (latng==null || latng=='' || latng==undefined) {
				// 腾讯定位
				that.getMyLocationTX();
			}else{
				// 地图选点定位
				let latngZH = [];
				latngZH = latng.split(",");
				localStorage.setItem('INlatitude',latngZH[0]);//纬度
				localStorage.setItem('INlongitude',latngZH[1]);//经度
				that.$uerstore.store.commit("addname",addr);//地址状态写入
			}	 
		},
		//定位
		getMyLocationTX() {
			var geolocation = new qq.maps.Geolocation("AZ6BZ-G5WWO-UGOWQ-STA4N-NQG72-2TB4X", "因学教育");
			geolocation.getLocation(this.showPosition, this.showErr,this.addstate);
		},
		showPosition(position) {
			localStorage.setItem('INlatitude',position.lat);//纬度
			localStorage.setItem('INlongitude',position.lng);//经度
			//重新获取内容
			this.$uerstore.store.commit("addname",position.city+position.addr);//地址状态写入
		},
		showErr() {
			console.log("定位失败");
			localStorage.setItem('INlatitude','30.230001');//纬度
			localStorage.setItem('INlongitude','120.213776');//经度
			//重新获取内容
			this.$uerstore.store.commit("addname",'杭州');//地址状态写入
		},
		//根据传入状态页面跳转
		INpagethis(){
			let pageurl = this.$route.query.INpagethisid;
			if (pageurl==1) {
				//已经报名机构页面
				this.$router.push({path:'/JGorder',query:{}});
			};
		}
	},
	watch: {//使用watch 监听$router的变化
	    $route(to, from) {
	      //如果to索引大于from索引,判断为前进状态,反之则为后退状态
	      if(to.meta.index > from.meta.index){
	        //设置动画名称
	        this.transitionName = 'slide-left';
	      }else{
	        this.transitionName = 'slide-right';
	      };
	    }
	  },
	mounted(){
	}
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
body,html{
	width: 100%;
	height: 100%;
	background-color: rgba(255,255,255,1);
}
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  transition: all 500ms;
  position: absolute;
}
.slide-right-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
.slide-right-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.slide-left-enter {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.slide-left-leave-active {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
</style>



